#index {
	height: 100%;
  background: url('../images/backgrounds/index.jpg');
  background-size: cover;
  background-color: #eff8f9;

  height: 100vh;
  padding: 40px 75px 25px 50px;
  -js-display: flex;
  display: flex;
  flex-direction: column;



  .left-side {
    -js-display: flex;
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
  }



  .content {
  	height: 100%;
    -js-display: flex;
  	display: flex;
  	// flex-direction: column;
  	padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

  	.main-content {
      -js-display: flex;
  		display: flex;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {
        -js-display: flex;
  			// display: flex;
	  		// flex: 1
	  	}



  		// .top {
        -js-display: flex;
  		// 	display: flex;
			 //  flex-flow: column;
				// height: 100%;
			 //  flex: 3;
			 //  // padding-top: 10%;

				// .headline {
				// 	// position: relative;
				// 	// top: 0;
				// 	// right: 0;


        -js-display: flex;
				// 	display: flex;
	  	// 		flex-direction: row;
  		// 		justify-content: flex-end;


  		// 		// .first {
  		// 		// 	width: 100%;
  		// 		// }
  		// 		// .break {
  		// 		// 	// width: 100%;
  		// 		// }
  		// 		// .second {
  		// 		// 	width: 100%;
  		// 		// }
				// }
  		// }

  		.divider {
  			flex: 1;
  		}

  		.heading {
  			color: #249fbe;
				font-size: 80.5px;
				text-transform: uppercase;
				font-family: 'Brandon Black';
        -js-display: flex;
			  display: flex;
			  flex-direction:column;
			  align-items:flex-end;
		    flex: 2;

			  .row {
			    display:flex;
			    flex-direction:row;
			    width:50%;

					margin-top: 30px;
					height: 55px;

					&:first-child {
						margin-top: 100px;
						padding-left: 110px;
					}

			    .box {
			      flex: 1;

			      &.big-flex {
			      	flex: 3;
			      }

			      &.text {
			        flex: 10 0 auto;
			      }

			      &.align-right {
			        text-align:right;
			      }
			    }
			  }
			}

  		.footer {
        -js-display: flex;
  			display: flex;
				align-items: center;

				.copyright {
					flex: 3;
					font-family: 'Proxima Nova';
					font-size: 14px;
					color: #9db7c1;
				}

				.copyright-mobile {
					display: none;
				}

  			.social {
					flex: 1;
          -js-display: flex;
  				display: flex;
					align-items: center;

					.box {
						margin: 8px;

						i {
							color: #1d9dbd;
							font-size: 15px;
						}
					}

  			}
  			.cta {
  				padding-left: 40px;
					font-family: 'Brandon Bold';
					font-size: 14px;

					a {
						text-decoration: none;
						color: #1d9dbd;
					}
  			}

  			ul.mobile-bottom-navigation {
  				display: none;
  			}
  		}
  	}
  }


}

.side-nav {
	position: absolute;
	top: 0;
	right: 40px;
	height: 100%;
	border-left: 1px solid #cadbe0;
}


@media @mobile,
@tablet {
  // @import 'media';
  #index {
  	// background-position: -140px 0;
  	background-size: cover;
    padding: 35px 20px 20px 20px;

    .content {
      .main-content {
        .heading {
      		justify-content: flex-end;
  				font-size: 250%;

          .row {
          	width: 100%;

          	.box {
				      &.big-flex {
				      	// flex: 0;
				      }
				    }
          }
        }
      }
    }
  }
}

@media @tablet {
	.heading {
		justify-content: flex-end;
		font-size: 250%;

    .row {
    	width: 100%;

    	.box {
	      &.big-flex {
	      	flex: 10 !important;
	      }
	    }
    }
  }
}

@media @mobile {
	.heading {
		justify-content: flex-end;
		font-size: 250%;

    .row {
    	width: 100%;

    	.box {
	      &.big-flex {
	      	flex: 0 !important;
	      }
	    }
    }
  }
}
