#article {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    // padding-top: 10vh;
    padding: 60px 0 0 0;
    & > * {
      // flex: 1
    }

    .headline {
      color: #249fbe;
      margin-top: -10px;
      font-size: 70px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      margin-bottom: 0;
      max-width: 90%;
      line-height: 1;
      padding-left: 50px;
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;

      .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: flex-start;
        overflow: auto;

        article {
          display: flex;
          align-items: flex-start;
          flex-shrink: 0;
          padding: 60px 0;

          .date {
            flex: 1;
            color: #b1ccd2;
          }

          .image {
            flex: 2;
          }

          .content {
            flex: 4;
            margin-left: 60px;
            padding: 0;

            font-family: 'Proxima Nova';
            color: #525a5d;

            p {
              &:first-child {
                margin-top: 0;
              }
            }

            ol {
                list-style: none;
                margin: 0;

                li:before {
                  @top-margin: 20px;
                  margin: @top-margin 0;
                  background: #b0d3de;
                  content: ' ';
                  width: 8px;
                  height: 8px;
                  display: inline-block;
                  font-style: italic;
                  border-radius: 50%;
                  margin: @top-margin 10px 0 0; /* Расстояние от маркера до текста */
                }
            }

          }
        }
      }

      .controls {
        display: flex;
        justify-content: center;

        a.arrow {
          transition: color .2s linear;
          cursor: pointer;

          &:hover > img {
            filter: ~"grayscale(100%) brightness(0)";
          }
        }
      }
    }
  }
}


@media @mobile,
@tablet {
  #blog {
    background-size: cover;
    padding: 0px;
    // padding: 35px 20px 20px 20px;

    .headline {
      font-size: 250% !important;
      padding: 0 0 25px 15px;
    }

    .content {
      .main-content {
        padding: 0;

        .content {
          overflow: visible !important;

          article {
            margin-bottom: 30px;
            width: 100%;
            padding: 0 15px;

            .image {
              flex: 1 !important;

              img {
                width: 25vw;
                height: 25vw;
              }
            }

            .content {
              flex: 1 !important;
              margin: 0 !important;


              h4 {
                font-size: 14px;
              }

              p {
                font-size: 12px;
              }

              .footer {
                flex-direction: row;
                font-size: 12px;
              }
            }
          }
        }


      }
    }

    footer {
      padding: 35px 20px 20px 20px;
    }
  }
}

@media @tablet {
  .heading {
    justify-content: flex-end;
    font-size: 250%;

    .row {
      width: 100%;

      .box {
        &.big-flex {
          flex: 10 !important;
        }
      }
    }
  }
}

@media @mobile {
  .heading {
    justify-content: flex-end;
    font-size: 250%;

    .row {
      width: 100%;

      .box {
        &.big-flex {
          flex: 0 !important;
        }
      }
    }
  }
}
