footer.footer {
  display: flex;
  align-items: center;

  .copyright {
    flex: 3;
    font-family: 'Proxima Nova';
    font-size: 14px;
    color: #9db7c1;
  }

  .copyright-mobile {
    display: none;
  }

  .social {
    flex: 1;
    display: flex;
    align-items: center;

    .box {
      margin: 8px;

      a {
        text-decoration: none;
        color: #1d9dbd;
        font-size: 15px;
      }
    }

  }
  .cta {
    padding-left: 40px;
    font-family: 'Brandon Bold';
    font-size: 14px;
    color: #1d9dbd;
  }

  ul.mobile-bottom-navigation {
    display: none;
  }
}

@media @mobile,
@tablet {
  .footer {
    flex-direction: column;
    align-items: flex-start !important;

    .copyright {
      display: none;
    }
    .copyright-mobile {
      order: 1;
      display: block !important;
      font-family: 'Proxima Nova';
      font-size: 14px;
      color: #9db7c1;
    }
    .social {
      order: 0;

      .box {
        a, a:visited {
          text-decoration: none;
          color: #1d9dbd !important;
        }

        &:first-child {
        margin-left: 0;
        }
      }
    }

    .cta {
      display: none;
    }

    ul.mobile-bottom-navigation {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex !important;

      &:before {
        content: ' ';
        height: 3px;
        width: 100px;
        background: red;
      }
    }
  }
}
