.navigation-horizontal {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  position: absolute;
  right: 8px;
  z-index: 3;
  top: 200px;

  ul.numbers {
    display: flex;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    right: 30px;
    position: relative;
    font-size: 20px;
    
    li {
      padding: 10px;
      // flex:.2;
      transform: rotate(270deg);
      a {
        text-decoration: none;
        color: #a8d0da;
        font-family: 'PFDinTextPro-Regular';

        &:hover, &.active {
          color: #1d9dbd;
        }
      }
    }

    li.leading-line {
      width: 140px;
      transform: rotate(0deg);

      &:before {
        content: ' ';
        background: #cbdbe0;
        height: 1px;
        width: 100px;
        display: block;
      }
    }
  }

  ul.arrows {
    display: flex;
    list-style: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    li {

      background: #249fbe;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;

      &.forward {
        width: 65px;
        height: 65px;
      }

      &.back {
        width: 35px;
        height: 35px;
      }

      &.divider {
        flex: 1 1 10px;
        background: none;
      }



      a {
        color: #fff;
        text-decoration: none;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &:hover > i {
          animation: arrow-shake .6s 1;
        }
      }


      @keyframes arrow-shake {
        0% {
          -webkit-transform: translate3d(-1.5px, 0, 0);
          transform: translate3d(-1.5px, 0, 0);
        }


        50% {
          -webkit-transform: translate3d(1.5px, 0, 0);
          transform: translate3d(1.5px, 0, 0);
        }

        100% {
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }
      }
    }
  }
}


@media @mobile,
@tablet {
  .navigation-horizontal, .side-nav {
    display: none;
  }
}

.side-nav {
  z-index: 1;
}
